  .formulario {
  background-color: #419ABE;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-height: 85vh;
  padding: 2.4%;
  width: auto;
  border-radius: 20px 20px 10px 10px;
  box-shadow: 2px 2px 7px -2px black;
  font-family:  "roboto", "sans-serif";
}

  .formulario select {
  font-size: 4.5vw;
}

  #sectionComBotoes {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 1% 3%;
}
  .formulario section {
  background-color: #fff;
  margin: 2.2% 0;
  padding: 3.7% 1.6%;
  min-height: auto;
  border-radius: 8px 8px 8px 8px;
  box-shadow: 3px 2px 6px -2px black;
}

  .formulario section button {
  font-size: 5.2vw;
  padding: 1.9%;
  min-width: 20%;
  background-color: #419ABE;
  color: #fff;
  font-weight: 500;
  border: none;
  border-radius: 4px 4px 4px 4px;
  transition-duration: 0.007s;
  width: 30%;
}

  .formulario section button:disabled:active {
  background-color: #AED4E3;
}

  .formulario section button:disabled {
  background-color: #AED4E3;
}

  .formulario section button:active {
  background-color: #7BB9D2;
}

  .sectionComTextarea {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

  .formulario textarea {
  font-size: 2.8vw;
  width: 60vw;
  height: 8vh;
  border: solid 2px black;
  border-radius: 5px 5px 5px 5px;
  outline: none;
  margin-left: 15%;
  resize: none;
}

  .sectionComInput_file img {
  width: 40%;
}

  .formulario section label {
  font-size: 4.3vw;
  color: #FD4F02;
  font-weight: 600;
  margin-right: 1.9%;
}

  .formulario section h1 {
  font-size: 7.4vw;
  border: solid 4px #419ABE;
  color: #FD4F02;
  text-align: center;
  border-radius: 5px 5px 5px 5px;
  font-weight: 800;
  padding: 1% 0;
}

  .sectionComInput {
  display: flex;
  flex-direction: row;
  justify-content: start;
  padding: 1% 0;
  align-items: center;
}

  .sectionComInput input {
  font-size: 3.5vw;
  height: 3vh;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  border-radius: 7px 7px 7px 7px;
  outline: none;
  width: 60%;
}





 @media screen and (min-width: 900px) {

   .formulario {
     width: 90%;
     display: grid;
     grid-template-columns: repeat(1, 1fr);
     grid-template-rows: repeat(6, auto);
     row-gap: 2%;
     column-gap: 1%;
     margin: 2%;
     grid-template-areas:

     "header header"

     "nome preco"

     "descricao descricao"

     "categoria imagem"

     "ingredientes restricoes"

     "botoes botoes";
  }

   #sectionHeader {
     grid-area: header;
     padding: 0 2%;
  }

   #sectionHeader #title {
     font-size: 3.2vw;
     border-width: 5px;
     text-align: center;
  }

   .sectionNome {
     grid-area: nome;
     flex-direction: column;
  }

   .sectionDescricao {
     grid-area: descricao;
     flex-direction: column;
  }

   .sectionPreco {
     grid-area: preco;
     flex-direction: column;
  }

   .sectionCategoria {
     grid-area: categoria;
     flex-direction: column;
  }

   .formulario section .labels {
     font-size: 2vw;
  }

   .sectionDescricao textarea {
      max-width: 80%;
      
  }

   .sectionCategoria label {
     display: block;
  }

   .sectionComInput_file {
     grid-area: imagem;
     flex-direction: column;
  }

   .sectionIngredientes {
     grid-area: ingredientes;
     flex-direction: column;
  }

   .sectionIngredientes #novosIngredientes {
     max-width: 80%;
  }

   .sectionRestricoes {
     grid-area: restricoes;
     flex-direction: column;
  }

   .sectionRestricoes #novasAlergias_restricoes {
     max-width: 80%;
  }

   #sectionComBotoes {
     grid-area: botoes;
     padding: 1%;
     display: flex;
     justify-content: space-between;
     align-items: center;
     background-color: #419ABE;
     box-shadow: none;
  }

   #sectionComBotoes button {
     width: 10%;
     height: 80%;
     font-size: 2.9vw;
     padding: 0;
     background-color: white;
     color: #419ABE;
  }

   #sectionComBotoes button:active {
     background-color: gray;
  }

   #sectionComBotoes button:disabled {
     background-color: #A5B2BB;
  }

   .formulario section {
     max-width: 100%;
     padding-top: 6%;
     margin: 0;
     max-height: 100%;
     box-shadow: 4px 3px 6px -2px black;
     border-radius: 8px;
     text-align: center;
  }

   .formulario section label {
     font-size: 2vw;
  }

   .formulario section:has(textarea) {
     text-align: center;
     border-radius: 6px;
  }

   .formulario section input[type="file"],
   .formulario section select {
     font-size: 1.9vw;
     text-align: center;
  }

   .formulario section img {
     width: 26.999%;
  }

   .formulario section :is(textarea, input) {
     border-width: 4px;
     font-size: 1.4vw;
     max-height: 30%;
  }

   .formulario section :is(textarea, select) {
     width: 60%;
     margin: 0;
  }

   .sectionComTextarea {
     flex-direction: column;
     align-items: center;
  }

   #novaDescricao {
  }

}
