 .container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: fixed;
  top: 30%;
  left: 14%;
  border-radius: 6px;
  border: solid black 3px;
  padding: 1.2%;
  width: 70%;
  min-height: auto;
  background-color: #fff;  
  font-family: "arial", "sans-serif";
}

.container section {
  margin: 2% 0;
  padding: 1.2%;
  height: 7vh;
  display: flex;
}

.container button {
  width: 100%;
  height: 100%;
  font-size: 5vw;
  flex-grow: 1;
  border-radius: 4px;
  transition-duration: 0.07s;
  background-color: #4DA0C2;
  color: white;
  font-weight: 500;
  border-width: 1px;
  border-style: solid;
  border-color: black;
}

.container button:active {
  border: solid 2px black;
  background-color: #63ACC9;
  color: #C2DEEA;
}

#section1 button {
  width: 27%;
  flex-grow: 0;
  font-size: 4.6vw;
  background-color: #4DA0C2;
  padding: 1.4% 2.3%;

}

#section1 {
  justify-content: space-between;
  height: 5vh;
}

#section1 b {
  font-size: 4.6vw;
  font-weight: 700;
}



 @media screen and (min-width: 900px) {

   .container {
     width: 40%;
     left: 25%;
  }

   #section1 :is(button, b) {
     font-size: 2vw;
  }

   #section2 button, #section3 button {
     font-size: 2vw;
  }

}
